import React from "react";
import '../Stylesheets/Register.css';
import $ from "jquery";
import firebase from "firebase";
import "firebase/auth";

export default function Register() {

  const SentRegisterData=() =>{
    
    var name = $('#Username').val();
    var email = $('#Email').val();
    var pw = $('#Password').val();
    var cpw = $('#CheckPassword').val();
    //ÂâçÁ´ØÂèñË≥áÊñô
    if (!name || !email || !pw || !cpw) {
      alert('Ë´ãËº∏ÂÖ•Êú™Â°´Ê¨Ñ‰Ωç!');
    } else if(email.search('@')===-1){
      alert('‰∏¶Êú™ÈõªÂ≠ê‰ø°ÁÆ±');
    } else if(pw.length<6){
      alert('ÂØÜÁ¢ºÂ§™Áü≠');
    } 
    else if (pw !== cpw) {
      alert('ÂÖ©Ê¨°Ëº∏ÂÖ•ÂØÜÁ¢º‰∏çÁõ∏Âêå!');
        //Èô§ÈåØ
    } else {
        var api = "/member/register";
        //url ÈÄ£Á∑öapi
        var data = {
            'name': name,
            'email': email,
            'password': pw,
            'checkpassword' : cpw,
        };
        //data
        $.post(api, data, function (res) {
            if (res.status === 0) {
                //cookie.set('zhjk217chatapp', data, { expires: 7, path: '/' });
                firebase
                  .auth()
                  .signInWithEmailAndPassword(email, pw)
                  .then((userCredential) => {
                    // Signed in
                    //var user = userCredential.user;
                    console.log(userCredential.user.uid);
                    alert("Ë®ªÂÜäÊàêÂäü");
                    // ...
                  })
                  .catch((error) => {
                  //var errorCode = error.code;
                  var errorMessage = error.message;

                  alert("Ë®ªÂÜäÂ§±Êïó"+errorMessage);
                   });
                  //post
            }
            else{
              alert(res.msg);
            }
            });
        //post
  };
};
  return (
    <div className="Register">
        <div>
            <input type="text" id="Username" placeholder="UserName"/>
        </div>
        <div>
          <input type="text" id="Email" placeholder="Email"/>
        </div>
        <div>
           <input type="password" id="Password" placeholder="Password"/>
        </div>
        <div>
            <input type="password" id="CheckPassword" placeholder="CheckPassword"/>
        </div>
        <div class="btn">
      <button onClick={SentRegisterData}>Register</button>
   
      </div>
     </div>
  );
}